<template>
  <div class="ContenidoComponente">
    <apexcharts
      width="100%"
      :type="this.type"
      :options="this.chartOptions"
      :series="this.series"
      height="300px"
      class="EstilosGrafico"
    ></apexcharts>
    <br />
  </div>
</template>

<script>
import VueApexCharts from "vue-apexcharts";

export default {
  name: "Grafico",
  components: {
    apexcharts: VueApexCharts,
  },
  watch: {
    DataEjeY: {
      // the callback will be called immediately after the start of the observation
      immediate: true,
      deep: true,
      handler(val, oldVal) {
        // do your stuff
        this.ActualizarGrafico();
      },
    },
  },
  data: function () {
    //console.log(this.$props);
    return {
      Records: {
        x: [],
        y: [],
      },
      //Grafico
      chartOptions: {
        chart: {
          id: "basic-bar",
          height: "500px"
        },
        xaxis: {
          categories: [],
          labels: {
            show: true,
            rotate: -45,
            rotateAlways: true,
            minHeight: 50
          },
        },
        yaxis: {
          title: {
            text: "this.DataEjeY",
          },
        },
        noData: {
          text: "Aún no hay datos...",
        },
        tooltip: {
          x: {
            format: 'dd MMM yyyy'
          }
        },  
      },
      series: [
        {
          name: this.TituloEjeX,
          data: [], //this.$props.DataEjeY, //.UltimosCincoItems(),
        },
      ],
    };
  },
  // props: ['TituloEjeY', 'DataEjeX', 'DataEjeY', "Acumulativo", "type"],
  props: {
    TituloEjeY: String,
    DataEjeX: Array,
    DataEjeY: Array,
    Acumulativo: Boolean,
    type: {
      type: String,
      default: "bar",
    },
    idHerramienta: {
      type: Number,
      default: 0,
    },
  },
  mounted() {
    this.ActualizarGrafico();
    //console.log("asda 1");
    // this.SepararClaveValor();
    //console.log(this.$props.DataEjeX);
  },
  methods: {
    //TODO: refactorizar un poco todo esto
    SetRecords() {
      if (this.idHerramienta != 5) {
        this.Records.x = this.DataEjeX;
        this.Records.y = this.DataEjeY;
        this.chartOptions.yaxis.title.text = "mililitros";
        return;
      }
      let now = new Date();
      this.Records.x = [];
      this.Records.y = [];
      for (let i = 0; i < this.DataEjeX.length; i++) {
        const dateDDMMYYYY = this.DataEjeX[i];
        let dateParts = dateDDMMYYYY.split("/");
        let recordDate = new Date(
          +dateParts[2],
          dateParts[1] - 1,
          +dateParts[0]
        );
        if (now.getFullYear() == recordDate.getFullYear()) {
          this.Records.x.push(recordDate.getTime());
          this.Records.y.push(this.DataEjeY[i]);
        }
      }

      let currentYear = new Date().getFullYear();
      this.chartOptions.yaxis.title.text = "metros";
      this.chartOptions.xaxis.type = "datetime";
      this.chartOptions.xaxis.min = new Date(`01 Jan ${currentYear}`).getTime();
      this.chartOptions.xaxis.max = new Date(`01 Jan ${currentYear + 1}`).getTime();
      this.chartOptions.yaxis.max = 0;
      this.chartOptions.yaxis.min = -5;
      // var parseToTimeValues = this.DataEjeX
      // .filter(stringDate => {
      //   let dateParts = stringDate.split("/");
      //   let recordDate = new Date(+dateParts[2],dateParts[1] - 1,+dateParts[0]);
      //   if (now.getFullYear() == recordDate.getFullYear()) return true;
      //   return false;
      // });
      // //console.log(parseToTimeValues);
      // parseToTimeValues = parseToTimeValues.map(stringDate => {
      //   let dateParts = stringDate.split("/");
      //   return new Date(+dateParts[2],dateParts[1] - 1,+dateParts[0]).getTime();
      // });
      // console.log(parseToTimeValues);
      // console.log(this.DataEjeY);
      // this.Records.x = parseToTimeValues;
    },
    ActualizarGrafico() {
      this.SetRecords();

      this.chartOptions.xaxis.categories = this.Records.x;
      this.chartOptions.chart = {
        toolbar: {
          show: false,
        },
      };
      this.chartOptions.yaxis.label = {
        style: {
          background: "#000",
          color: "#777",
          fontSize: "19px",
          fontWeight: 400,
        },
      };
      this.chartOptions.plotOptions = {
        bar: {
          horizontal: false,
          dataLabels: {
            position: "top",
          },
        },
      };
      this.chartOptions.dataLabels = {
        enabled: true,
        offsetY: 0,
        style: {
          fontSize: "12px",
          colors: ["#000"],
        },
        formatter: function (val) {
          if (val <= -5) return "...";
          return val;
        },
      };

      /////
      // this.chartOptions = {
      //   chart: {
      //     toolbar: {
      //       show: false
      //     },
      //   }
      //   xaxis: {
      //     categories: this.Records.x,
      //     // type: "datetime",
      //     // min: new Date("01 Jan 2022").getTime(),
      //     // max: new Date("01 Jan 2023").getTime(),
      //     // tickAmount: 1,
      //   },
      //   yaxis: {
      //     label:{
      //       style: {
      //         background: '#000',
      //         color: '#777',
      //         fontSize: '19px',
      //         fontWeight: 400,
      //       },
      //     },
      //   },
      //   plotOptions: {
      //     bar: {
      //       horizontal: false,
      //       dataLabels: {
      //         position: 'top',
      //       },
      //     }
      //   },
      //   dataLabels: {
      //     enabled: true,
      //     offsetY: 0,
      //     style: {
      //       fontSize: '12px',
      //       colors: ['#000']
      //     },
      //     formatter: function (val) {
      //       if (val <= -5) return "...";
      //       return val;
      //     },
      //   },
      // };

      let _Acumulativo = [];
      var _Aux = 0;
      if (this.Acumulativo) {
        this.Records.y.forEach((_Reg) => {
          _Aux += _Reg;
          _Acumulativo.push(_Aux);
        });
        this.series = [
          {
            name: "Individual",
            data: this.Records.y,
          },
          {
            name: "Acumulativo",
            data: _Acumulativo, //this.$props.DataEjeY, //.UltimosCincoItems(),
          },
        ];
      } else {
        this.series = [
          {
            name: "Altura napa",
            data: this.Records.y,
          },
        ];
      }
    },
  },
};
</script>


<style lang="scss" scoped>
.ContenidoComponente {
  display: flex;
  align-items: center;
  justify-content: center;
}
.EstilosGrafico {
  width: calc(100% - 20px);
  max-width: 600px;
}
@media (max-width: 360px) {
  .EstilosGrafico {
    width: calc(100% - 10px);
    max-width: 360px;
  }
}
</style>